.left__content{
    display: grid;
    justify-content: left;	
    height: auto;
    width: 576px;
    height: 208px;
    grid-gap: 38px 0px;
}

h1{
    font-size: 80px;
    font-weight: bold;
    line-height: 104px;
    line-height: 100%;
    text-align: left;
    vertical-align: top;
    margin-top: 0px;
}

p{
    font-size: 16px;
    font-style: regular;
    line-height: 24px;
    text-align: left;
    vertical-align: top;
}

#root > div > div > div.left__content > section > a:nth-child(n){
    color: #ffffff;
    display: block;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    word-spacing: 52px;
    width: fit-content;
    text-decoration: none;
}

#root > div > div > div.left__content > section > ul > li:nth-child(n) > span{
    background-color: lime;
    height: 50px;
    width: 100%;
}