@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Mono:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i&display=swap);
body{
    color: #ffffff;
    background-color: #292929;
    padding: 0px;
    margin: 0px;
};

body::-webkit-scrollbar {
    width: 0em;
}
body::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 0px red;
}
body::-webkit-scrollbar-thumb {
  background-color: red;
  outline: 1px solid red;
}

.App{
    padding: 96px 0px 56px 0px;
}
*{
    font-family: 'IBM Plex Mono' ;
}

.nav__content {
    display: grid;
    grid-template-columns: 576px 656px;
    grid-gap:  0px 208px;
    width: 1440px;
    margin: 0 auto;
}



.grid__container{
    display: grid;
    grid-template-columns: 656px;
    grid-template-rows: 232px;
    grid-gap: 16px 0px ;
    overflow-y: auto;
    overflow-x: hidden;
    height: 728px;
}

.grid__container > a:nth-child(n){
  background: #5E5E5E;
  text-align: center;
  text-decoration: none;
  -webkit-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  height: 232px;
}



.grid__container::-webkit-scrollbar {
  width: 0.4em;
}
.grid__container::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px transparent;
}
.grid__container::-webkit-scrollbar-thumb {
background-color: #BBCDE5;
}

.content__link{
  display: none;
}

.left__content{
    display: grid;
    justify-content: left;	
    height: auto;
    width: 576px;
    height: 208px;
    grid-gap: 38px 0px;
}

h1{
    font-size: 80px;
    font-weight: bold;
    line-height: 104px;
    line-height: 100%;
    text-align: left;
    vertical-align: top;
    margin-top: 0px;
}

p{
    font-size: 16px;
    font-style: regular;
    line-height: 24px;
    text-align: left;
    vertical-align: top;
}

#root > div > div > div.left__content > section > a:nth-child(n){
    color: #ffffff;
    display: block;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    word-spacing: 52px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    text-decoration: none;
}

#root > div > div > div.left__content > section > ul > li:nth-child(n) > span{
    background-color: lime;
    height: 50px;
    width: 100%;
}
